import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import {CookieService} from 'ngx-cookie-service';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.less']
})
export class LoginComponent implements OnInit {

  username:string='';
  password:string='';
  constructor(private router:Router,private cookie : CookieService ) { }

  ngOnInit() {
    console.log(this.password);
    console.log(this.username);
    if(this.cookie.get("userName") && this.cookie.get("passWord"))
    {
      this.router.navigateByUrl("home");
    }
    
  }
  Login()
  {
    this.cookie.set("userName",this.username);
    this.cookie.set("passWord",this.password);
    this.cookie.set("accessuserName","admin");
    this.cookie.set("accessPwd","12345");
    
    if((this.username==this.cookie.get("accessuserName"))&&(this.password==this.cookie.get("accessPwd")))
    {
      this.router.navigateByUrl("home");
      return true;  
    }
    this.cookie.deleteAll();
    return false;
  }
}
